@media only screen and (min-width: 1224px) and (max-width: 1332px) {
  /* Styles */
  #Pipeline {
    display: flex;
    margin: 0px 0px;
    margin-top: 0px;
  }
  .littleBody {
    color: white;
    top: 6px;
    position: relative;
    left: 3vw;
    width: 32vw;
    font-size: 1.2vw;
    display: table-caption;
  }
  .littleHeading {
    color: white;
    font-size: 28px;
  }
}

@media only screen and (min-width: 1332px) and (max-width: 1580px) {
  /* Styles */
  #Pipeline {
    display: flex;
    margin: 0px 65px;
    margin-top: 0px;
  }
  #footerMain {
    position: relative;
    left: 2vw;
  }
  .littleBody {
    color: white;
    top: 7px;
    position: relative;
    left: 55px;
    width: 37vw;
    font-size: 1.2vw;
    display: table-caption;
  }
}

@media only screen and (min-width: 1580px) {
  #main {
    font-size: 7vh;
    left: 30vh;
  }

  #leftover {
    top: 30vh;
    left: -29vh;
    font-size: 23px;
  }

  #page2 {
    margin-top: 70vh;
  }

  #main2 {
    font-size: 70px;
  }

  #scroll {
    margin-left: 60vh;
  }

  #services div {
    max-width: 70vh;
  }

  .littleHeading {
    font-size: 40px;
  }

  .littleBody {
    left: 6vh;
    width: 620px;
    font-size: 18px;
  }

  #back1,
  #back2,
  #back3,
  #back4,
  #back5,
  #front1,
  #front2,
  #front3,
  #front4,
  #front5 {
    background-size: 750px;
    width: 750px;
    height: 750px;
  }

  .slider {
    width: 750px;
  }

  .images {
    height: 550px;
    margin-left: 280px;
  }

  #scroll {
    height: 650px;
  }

  #page3 {
    margin-top: 250px;
  }

  #down_arrow {
    left: 48vh;
  }

  #page3_cards {
    margin: 10px 35vh;
  }

  .page3_heading {
    font-size: 30px;
  }

  #page3_body {
    font-size: 20px;
  }

  #testimonial {
    width: 18%;
    height: 20vh;
    margin: 50px 8vh;
  }

  #TestimonialWriting {
    font-size: 20px;
    margin: 0 auto;
    width: 80%;
    top: -1vh;
  }

  #testimonialName {
    margin-top: 10vh;
    font-size: 22px;
  }

  #Pipeline {
    margin: 0 25vh;
  }

  #PipelineCard0,
  #PipelineCard1,
  #PipelineCard2,
  #PipelineCard3,
  #PipelineCard4,
  #PipelineCard5 {
    margin: 60px 1vh;
    height: 350px;
    width: 400px;
  }

  #PipelineHeading {
    font-size: 35px;
  }

  #PipelineBody {
    margin: 0 2vh;
    margin-top: 30px;
    font-size: 20px;
  }

  #PipelineMain {
    font-size: 50px;
  }

  #Footer {
    margin-left: 28vh;
  }

  #footerMain > p,
  #footerMain > a,
  #col1 > a {
    font-size: 20px;
  }

  .navBar {
    font-size: 30px;
    padding: 0 20px;
    padding-top: 20px;
  }

  #companyName {
    font-size: 35px;
  }

  #col1 > h2,
  #footerMain > h1 {
    font-size: 40px;
  }

  #Testimonial > h1 {
    font-size: 50px;
  }

  #PipelineWrapper {
    padding-top: 50px;
    height: 700px;
    margin-bottom: 5vh;
  }
}
